import "../style/section.css";
import { Header } from "../blocks/Header";

export function AboutUs(props) {
  return (
    <main>
      <section className="welcome-section">
        <div className="wrapper">
          <div className="welcome-section__conteiner">
            <div className="first-page">
              <h2 className="title-first black">О нас</h2>
            </div>
            <div className="second-page">
              <p className="desc black">
                <h3 className="title-second">
                  <b>Предприятие имеет богатый опыт реализации:</b>
                </h3>
                <ul>
                  <li>
                    <p className="title-second">Жилых домов;</p>
                  </li>
                  <li>
                    <p className="title-second">
                      Комплекса наружных инженерных коммуникаций;
                    </p>
                  </li>
                  <li>
                    <p className="title-second">Подземных парковок;</p>
                  </li>
                  <li>
                    <p className="title-second">Детских садов</p>
                  </li>
                  <li>
                    <p className="title-second">
                      Объектов спортивного назначения
                    </p>
                  </li>
                </ul>
                <p className="title-second">
                  Построенные объекты отличаются высоким качеством.
                </p>

                <p className="title-second">
                  ООО «Легион» имеет Свидетельство СРО АСО ПОСО,
                  СРО-С-227-01072010, от 10.09.19 г., № 105
                </p>
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="why-us">
        <div className="wrapper wrapper__center">
          <span className="wrapper__flex">
            <div className="why-us__conteiner flex-left">
              <h3>
                ООО «Легион» предлагает услуги генподрядчика, технического
                заказчика, субподрядчика по устройству по следующим
                направлениям:
              </h3>
              <ul className="list flex-left">
                <li className="item why-us__item item__text">
                  жилые (дома, общежития, гостиницы, дома отдыха и т.п.)
                </li>
                <li className="item why-us__item item__text">
                  общественные (детские сады, школы, объекты спортивного
                  назначения, торговые центры, кинотеатры, театры, и т.п.)
                </li>
                <li className="item why-us__item item__text">
                  комплекс наружных инженерных коммуникаций
                </li>
                <li className="item why-us__item item__text">
                  административные (бизнес-центры и другие офисные здания)
                </li>
                <li className="item why-us__item item__text">
                  производственные (производственные и сборочные цеха заводов,
                  фабрик)
                </li>
                <li className="item why-us__item item__text">
                  энергетического хозяйства (здания ЦТП, котельные,
                  компрессорные, трансформаторные подстанции, распределительные
                  подстанции и т.п.)
                </li>
                <li className="item why-us__item item__text">
                  транспортно-складского хозяйства (склады, гаражи, депо и
                  т.п.)
                </li>
                <li className="item why-us__item item__text">
                  подсобные и вспомогательные (административные, бытовые и
                  т.п.).
                </li>
              </ul>
            </div>
            <div className="why-us__conteiner flex-left">
              <h3>Для успешного выполнения поставленных задач имеем:</h3>
              <ul className="list flex-left">
                <li className="item why-us__item item__text">
                  многолетний опыт работы в качестве генерального подрядчика и
                  субподрядчика;
                </li>
                <li className="item why-us__item item__text">
                  штат высококвалифицированных сотрудников и качественную
                  материально-техническую базу;
                </li>
                <li className="item why-us__item item__text">
                  ряд лучших партнеров и поставщиков, проверенных временем;
                </li>
                <li className="item why-us__item item__text">
                  опыт работы с различными многоотраслевыми Заказчиками
                </li>
                <li className="item why-us__item item__text">
                  опыт работы Техническим Заказчиком по строительству жилых
                  домов, Торговых Центров, Промышленных объектов
                </li>
                <li className="item why-us__item item__text">
                  сертифицированную систему менеджмента качества по ISO
                  9001:2015;
                </li>
              </ul>
            </div>
          </span>
        </div>
      </section>
    </main>
  );
}
