import "../style/section.css";
import { Header } from "../blocks/Header";

export function Home(props) {
  return (
    <main>
      <section className="welcome-section">
        <Header {...props} />
        <div className="wrapper">
          <div className="welcome-section__conteiner">
            <div className="first-page">
              <h2 className="title-first black">
              Строим как себе
              </h2>
              <h3 className="title-second black">
              Для нас не бывает чужих. Мы одинаково уважительно относимся к любым предложениям и запросам независимо от степени сложности и бюджета.
              </h3>
            </div>
            <div className="second-page">
              <h3 className="title-second"><b>Почему мы:</b></h3>
            </div>
          </div>
        </div>
      </section>
      <section className="why-us">
        <div className="wrapper wrapper__center">
          <div className="why-us__conteiner">
            <ul className="list">
              <li className="item item__text">
                В нашем портфеле - строительство жилья, офисных зданий, складов
                и прочих общественных зданий, фермерских построек, строительство
                сооружений, ирригационные системы, системы водоснабжения и сетей
                водоотведения, промышленные предприятия, трубопроводы, линии
                связи и электропередачи, спортивные сооружения и т.д.
              </li>
              <li className="item item__text">
                <ul>
                  Монтаж инженерных сетей любой сложности. Мы являемся
                  профессионалами в строительстве и ремонте:
                  <li>-ИТП и теплосети;</li>
                  <li>-комплексы энергоснабжения с линиями до 35 кВт;</li>
                  <li>-коммуникации связи;</li>
                  <li>
                    -водопроводные системы (горячее и холодное водоснабжение);
                  </li>
                  <li>
                    -сооружения ливневой и хозяйственно-бытовой канализации;
                  </li>
                  <li>-другие наружные сооружения инженерного обеспечения;</li>
                </ul>
              </li>
              <li className="item item__text">
                <ul>
                  На этапе планирования мы занимаемся:
                  <li>-проведением изыскательных работ;</li>
                  <li>-технико-экономическим обоснованием;</li>
                  <li>-составлением проектной документации;</li>
                  <li>
                    -согласованием проекта и проведением технической экспертизы;
                  </li>
                  <li>
                    -каждый этап по стоимости и по срокам реализации
                    согласовывается с заказчиком. При работе с нами вы не
                    получаете никаких персональных менеджеров, вы получаете
                    команду. Что снижает конечную стоимость проекта и дает
                    гарантию выполнения всех работ в оговоренные сроки;
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </section>
    </main>
  );
}
