import "../style/section.css";
import { Header } from "../blocks/Header";
import LicensesOne from "../assets/img/Licenses1.jpg";
import LicensesTwo from "../assets/img/Licenses2.jpg";
import LicensesThree from "../assets/img/Licenses3.jpg";
import LicensesFour from "../assets/img/Licenses4.jpg";
import LicensesFive from "../assets/img/Licenses5.jpg";
import LicensesSix from "../assets/img/Licenses6.jpg";

export function Licenses(props) {
  return (
    <main>
      <section className="welcome-section">
        < Header {...props}/>
        <div className="licenses__conteiner">
          <a href={LicensesOne} className="licenses__link" target="_blank">
            <img src={LicensesOne} alt="Licenses" className="licenses__img" />
          </a>
          <a href={LicensesTwo} className="licenses__link" target="_blank">
            <img src={LicensesTwo} alt="Licenses" className="licenses__img" />
          </a>
          <a href={LicensesThree} className="licenses__link" target="_blank">
            <img src={LicensesThree} alt="Licenses" className="licenses__img" />
          </a>
          <a href={LicensesFour} className="licenses__link" target="_blank">
            <img src={LicensesFour} alt="Licenses" className="licenses__img" />
          </a>
          <a href={LicensesFive} className="licenses__link" target="_blank">
            <img src={LicensesFive} alt="Licenses" className="licenses__img" />
          </a>
          <a href={LicensesSix} className="licenses__link" target="_blank">
            <img src={LicensesSix} alt="Licenses" className="licenses__img" />
          </a>
        </div>
      </section>
    </main>
  );
}
