import { useState } from 'react';
import './App.css';
import { Header } from './blocks/Header';
import { Home } from './components/Home';
import { AboutUs } from './components/AboutUs';
import { Licenses } from './components/Licenses';
import { Devilery } from './components/Devilery';
import { Control } from './components/Control';
import { Activities } from './components/Activities';
import { Projects } from './components/Projects';
import { Contacts } from './components/Contacts';

function App() {

  let [page, setPage] = useState('Home');

  let handdlePage = (page) => {
    setPage(page);
  }

  return (
    <div className="App">
      {page == 'Home' && < Home handdlePage={handdlePage}/>}
      < AboutUs />
      {page == 'Licenses' &&  <Licenses handdlePage={handdlePage}/>}
      {page == 'Devilery' &&  <Devilery handdlePage={handdlePage}/>}
      {page == 'Control' && < Control handdlePage={handdlePage}/>}
      {page == 'Activities' && < Activities handdlePage={handdlePage}/>}
      < Projects handdlePage={handdlePage}/>
      < Contacts handdlePage={handdlePage}/>

    </div>
  );
}

export default App;
