import "../style/section.css";
import { Header } from "../blocks/Header";
import blag1 from "../assets/img/blag1.jpg";
import blag2 from "../assets/img/blag2.jpg";
import gab1 from "../assets/img/Gab1.jpg";
import gab2 from "../assets/img/Gab2.jpg";
import gab3 from "../assets/img/Gab3.jpg";
import inj1 from "../assets/img/inj1.jpg";
import inj2 from "../assets/img/inj2.jpg";
import kap1 from "../assets/img/kap1.jpg";
import kap2 from "../assets/img/kap2.jpg";
import kap3 from "../assets/img/kap3.jpg";
import otd1 from "../assets/img/otd1.png";
import otd2 from "../assets/img/otd2.png";
import otd3 from "../assets/img/otd3.png";
import kosm1 from "../assets/img/kosm1.jpg";
import kosm2 from "../assets/img/kosm2.jpg";
import kosm3 from "../assets/img/kosm3.jpg";

export function Projects(props) {
  return (
    <main>
      <section className="welcome-section">
        <div className="wrapper">
          <div className="welcome-section__conteiner">
            <div className="first-page">
              <h2 className="title-first black">Наши проекты</h2>
            </div>
            <div className="second-page">
              <h3 className="title-second black">
                <b>Мы уже реализовали проекты в сферах:</b>
              </h3>
              <ul>
                <li>
                  <p className="title-second">
                    Устройство габионов и подпорных стенок
                  </p>
                </li>
                <li>
                  <p className="title-second">Инженерные сети</p>
                </li>
                <li>
                  <p className="title-second">Капитальное строительство</p>
                </li>
                <li>
                  <p className="title-second">Благоустройство</p>
                </li>
                <li>
                  <p className="title-second">Отделочные работы</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
      <section className="why-us why-us__bg">
        <div className="wrapper">
          <div className="why-us__conteiner">
            <h2 className="title-first">Подробнее</h2>
            <ul className="list">
              <li className="item item__text">
                Инженерные сети:
                <br /> Вынос сетей БП Румянцево и Котельная ТРЦ «Новомосковский»
                БЦ Румянцево
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={inj1}
                      alt="Инженерные сети"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={inj2}
                      alt="Инженерные сети"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
              <li className="item item__text">
                Устройство габионов и подпорных стенок:
                <br /> Футура парк, Новая рига Габионы и водопропускные трубы
                трассы М-4 ДОН
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={gab1}
                      alt="Устройство габаритов"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={gab2}
                      alt="Устройство габаритов"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={gab3}
                      alt="Устройство габаритов"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
              <li className="item item__text">
                Капитальное строительство:
                <br /> Жилой комплекс по ул. Малой Ямской, г. Нижнего Новгорода
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={kap1}
                      alt="Капитальное строительство"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={kap2}
                      alt="Капитальное строительство"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={kap3}
                      alt="Капитальное строительство"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
              <li className="item item__text">
                Благоустройство:
                <br /> Берегоукрепление трасса М-4 «Дон» Благоустройство ЖД по
                ул. Малой Ямской, Нижний Новгород
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={blag1}
                      alt="Благоустройство"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={blag2}
                      alt="Благоустройство"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
              <li className="item item__text">
                Отделочные работы:
                <br /> Ай Флай, Letoile, МРТС, Энсис
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={otd1}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={otd2}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={otd3}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
              <li className="item item__text">
                Выполнение комплекса наружных инженерных коммуникаций:
                <br /> Космодром Восточный
                <ul className="list__image">
                  <li className="item__image">
                    <img
                      src={kosm1}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={kosm2}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                  <li className="item__image">
                    <img
                      src={kosm3}
                      alt="Отделочные работы"
                      className="proj__image"
                    />
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </section>
    </main>
  );
}
