import "../style/section.css";
import { Header } from "../blocks/Header";

export function Devilery(props) {
  return (
    <main>
      <section className="welcome-section">
        <Header {...props} />
        <div className="wrapper">
          <div className="welcome-section__conteiner">
            <div className="first-page">
              <h2 className="title-first black">Доставка материалов</h2>
            </div>
            <div className="second-page">
              <ul>
                <li>
                  <p className="desc item__text">
                    Компания ООО «Легион» осуществляет доставку материалов до
                    объекта с помощью собственного автопарка.
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Вы можете быть полностью уверенны, что материалы будут
                    доставлены в срок, и Вам не нужно будет думать куда
                    складывать большое количество материалов, так как все будет
                    подвозиться постепенно, по мере выполнения работ.
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Так же компания осуществляет доставку чистовых материалов
                    приобретенных Вами в других точках продаж, если вдруг
                    чего-то не оказалось в нашем каталоге (цена доставки
                    оговаривается отдельно).
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Мы стараемся максимально упростить и ускорить ремонт,
                    максимально облегчив Вам задачу в подборе и доставке
                    материалов и сократить потраченное на ремонт время.
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}
