import "../style/section.css";
import { Header } from "../blocks/Header";

export function Control(props) {
  return (
    <main>
      <section className="welcome-section">
        <Header {...props} />
        <div className="wrapper">
          <div className="welcome-section__conteiner">
            <div className="first-page">
              <h2 className="title-first black">Контроль качества</h2>
            </div>
            <div className="second-page">
              <ul>
                <li>
                  <p className="desc item__text">
                    Компания "Легион" гордится качеством предоставляемых услуг ,
                    это связано с наличием независимых экспертов , которые
                    постоянно следят за ходом работ и проверяют правильность их
                    выполнения.
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Мы имеет отличный опыт на основе многолетних методик по
                    проверке качества. С помощью разработанных систем проверки
                    нашей компании , мы добиваемся максимального качественного
                    предоставления услуг.
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Все материалы проверяются на специальном сертифицированном
                    оборудовании, по этому не допускается доставка
                    некачественной продукции на место ремонта.
                  </p>
                </li>
                <li>
                  <p className="desc item__text">
                    Так же , что является одним из основных плюсов работы с
                    нами, это не будет влиять на увеличение стоимости заказанных
                    вами услуг. Подробности Вы можете узнать у наших
                    специалистов , заполнив форму обратной связи или позвонить
                    по телефону.
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}
